import facebook from '../assets/facebook.svg';
import twitter from '../assets/twitter.svg';
import instagram from '../assets/instagram.svg';
import logo from '../assets/logo.svg';
import { Link } from 'react-router-dom';
import { BsFacebook, BsTwitter, BsDiscord, BsLinkedin } from 'react-icons/bs';
import { AiFillInstagram } from 'react-icons/ai';

export default function Footer() {
    return (
        <>
            <div className="bg-[#D3D3D3] text-[#535353]">
                <div className="flex flex-col items-center justify-center">
                    <div className="sm:hidden block bg-gradient-to-r to-[#DA6732] from-[#D93B47] py-2 sm:mt-4 sm:px-11 mt-8 px-4 sm:text-[14px] rounded-full hover:scale-105 transition-all hover:shadow-md">
                        <p className=' text-backgroundPrimary sm:font-medium font-semibold'>Download App</p>
                    </div>
                    <p className="sm:py-6 py-4 text-sm sm:text-[16px] sm:block hidden">Toonsutra coming soon on Google and Apple</p>
                    <div className="sm:block hidden">
                        <div className='flex'>
                            <img src={require("../assets/android.png")} alt="android" className=" sm:h-12 h-9 px-2" />
                            <img src={require("../assets/ios.png")} alt="ios" className="sm:h-12 h-9 px-2" />
                        </div>
                    </div>
                    <div className='flex sm:py-2 sm:pt-6 pt-6 text-[#535353] text-[40px]'>
                        <a href="https://www.instagram.com/toonsutra.webtoons" target="_blank" rel="noreferrer">
                            <AiFillInstagram className='px-2 hover:scale-110 cursor-pointer text-[44px] -mt-[2px]' />
                        </a>
                        <a href="https://www.facebook.com/Toonsutra.webtoons.page" target="_blank" rel="noreferrer">
                            <BsFacebook className='px-2 hover:scale-110 cursor-pointer' />
                        </a>
                        <a href="https://twitter.com/toonsutra" target="_blank" rel="noreferrer">
                            <BsTwitter className='px-2 hover:scale-110 cursor-pointer' />
                        </a>
                        <a href="https://www.linkedin.com/company/89716802/admin/" target="_blank" rel="noreferrer">
                            <BsLinkedin className='px-2 hover:scale-110 cursor-pointer' />
                        </a>
                        <a href="https://discord.gg/wrReXQj4" target="_blank" rel="noreferrer">
                            <BsDiscord className='px-2 hover:scale-110 cursor-pointer' />
                        </a>
                    </div>
                    <div className="flex py-6 sm:flex-row flex-col sm:text-left text-center">
                        <Link to='/' className="px-[20px] text-sm sm:text-[16px]">Home</Link>
                        <Link to='/library' className="px-[20px] text-sm sm:text-[16px]">Library</Link>
                        <Link to='/privacy-policy' className="px-[20px] text-sm sm:text-[16px]">Privacy Policy</Link>
                        <Link to='/' className="px-[20px] text-sm sm:text-[16px]">Contact Us</Link>
                        <Link to='/faq' className="px-[20px] text-sm sm:text-[16px]">FAQs</Link>
                    </div>
                    <div className="py-2">
                        <img src={logo} alt="logo" />
                    </div>
                </div>
            </div>
        </>
    )
}