import Footer from "../components/Footer";
import { NavBar } from "../components/NavBar";
import { useState, useEffect } from 'react';

export default function FAQ() {
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    return (
        <div className="div">
            <NavBar currentPage={"FAQs"} />
            <div className="sm:mx-16 mx-4 sm:my-10 my-4 px-8 py-6 bg-white text-left text-black">
                <h1 className="sm:text-[40px] text-2xl font-medium">FAQs  (temporary placeholder, until content is shared)</h1>
                <Heading text="1. What is Toonsutra?" />
                <Paragraph text={"Toonsutra is Indias new storytelling home for disruptive creators to experiment, innovate and create engaging mobile webtoon comic stories and feature the biggest comic content from India and the world."} />
                <Heading text="2) Is the Toonsutra app free?" />
                <Paragraph text="The Toonsutra app is free to download and all titles are free to read and download except for the Special editions. To unlock Special Editions you need coins. You can earn coins to unlock the Special chapters and they will be available to you for as long as the series is on Toonsutra" />
                <Heading text="3) How do I subscribe to my favourite series?" />
                <Paragraph text="Select a series and tap the Subscribe+ button below the display banner. Youll receive push notifications on your app whenever new chapters are updated. " />

            </div>
            <Footer />
        </div>
    );
}

function Paragraph({text}) {
    return (<p className="sm:text-base text-xs font-normal my-4 text-textPrimary">{text}</p>);
}

function Heading({text}) {
    return (<h1 className="text-[20px] text-textPrimary sm:leading-[0px] mt-12">{text}</h1>);
}